        url("../../assets/fonts/Nunito-Regular.ttf") format("truetype");
}
@font-face {
    font-family: "Nunito";
    font-weight: 600;
    src: local("Nunito-Bold"),
        url("../../assets/fonts/Nunito-Bold.ttf") format("truetype");
}
@font-face {
    font-family: "Nunito";
    font-weight: 900;
    src: local("Nunito-Black"),
        url("../../assets/fonts/Nunito-Black.ttf") format("truetype");
}